import { OFFER_STEPS } from 'configs/constants';
import _get from 'lodash/get';
import { Offer } from 'types';
import _isEmpty from 'lodash/isEmpty';
import { formatCurrency } from './Currency';
import { formatDate } from './Date';

export const isOfferActionStep = (offer: Offer, expectationStep: number) =>
  _get(offer, 'CurrentStep') === expectationStep;

export const isPABeingSigned = (offer: Offer): boolean =>
  _get(offer, 'CurrentStep') === OFFER_STEPS.paBeingSigned;

export const isDocumentExits = (offer: Offer, type: string): boolean =>
  !_isEmpty(_get(offer, type));

export const getSellers = (value: Array<string>): string =>
  value ? value.join(', ') : '';

export const getOfferPrice = (offer: Offer): string => {
  let price;

  if (_get(offer, ['CurrentStep']) === OFFER_STEPS.counteredBySeller)
    price = _get(offer, [
      'Offer',
      'CounterOffer',
      'SellerCounter',
      'OfferPrice',
    ]);
  else
    price = _get(offer, [
      'Offer',
      'CounterOffer',
      'BuyerCounter',
      'OfferPrice',
    ]);

  return price ? formatCurrency(price) : '';
};

export const getDownPayment = (offer: Offer): string => {
  const downPayment = _get(offer, ['Offer', 'Funding', 'DownPayment']);
  return downPayment ? formatCurrency(downPayment) : '';
};

export const isDisplayContingencyTable = (step: number): boolean => {
  return step >= OFFER_STEPS.confirmTransfer;
};

export const isShowStep200 = (currentStep: number): boolean =>
  currentStep === OFFER_STEPS.paBeingSigned;

export const isStep = (currentStep: number, expectationStep: number): boolean =>
  currentStep === expectationStep;

export const getDate = (value: string): string => value && formatDate(value);

export const getStatus = (dueDate: string): string => {
  if (dueDate) {
    return formatDate(new Date().toDateString()) <= formatDate(dueDate)
      ? 'In Process'
      : 'OVERDUE';
  }
  return '';
};
