import * as Survey from 'survey-knockout';
import { CATEGORY, PROPERTY, PROPERTY_TYPE } from '../Constants';
import _get from 'lodash/get';
import {
  rounder,
  getTitleFee,
  getMonetaryTotalAmount,
  triggerMonetaryData,
} from '../Shared/Expression';

const serializer = _get(Survey, 'Serializer');
const functionFactory = _get(Survey, ['FunctionFactory', 'Instance']);

serializer.addProperty('survey', {
  name: `${PROPERTY.action}:number`,
  category: CATEGORY.general,
});

serializer.addProperty('survey', {
  name: `${PROPERTY.submitOnConfirmation}:boolean`,
  category: CATEGORY.confirmation,
});

serializer.addProperty('survey', {
  name: `${PROPERTY.confirmationMessage}:text`,
  category: CATEGORY.confirmation,
  dependsOn: [PROPERTY.submitOnConfirmation],
  visibleIf: (obj: any) => obj.submitOnConfirmation,
});

serializer.addProperty('question', {
  name: `${PROPERTY.questionId}:text`,
  category: CATEGORY.general,
  readOnly: true,
  default: '',
});

[rounder, getTitleFee, getMonetaryTotalAmount, triggerMonetaryData].map(func =>
  functionFactory.register(`${func.name}`, func)
);

serializer.addProperty('text', {
  name: `prefix:text`,
  category: CATEGORY.general,
});

serializer.addProperty('panel', {
  name: `${PROPERTY.panelCssType}:${PROPERTY_TYPE.dropdown}`,
  category: CATEGORY.general,
  choices: ['borderaround|Border Around', 'default|Default'],
});

serializer.addProperty('expression', {
  name: `${PROPERTY.expressionCssType}:${PROPERTY_TYPE.dropdown}`,
  category: CATEGORY.general,
  choices: ['colorfilled|Color Filled', 'default|Default'],
});
