import React, { FC } from 'react';
import {
  Edit,
  EditProps,
  SimpleForm,
  TextField,
  Toolbar,
  SaveButton,
  FileInput,
  FileField,
  useNotify,
  SaveButtonProps,
  useDataProvider,
  FormDataConsumer,
  required,
  useRefresh,
} from 'react-admin';
import { useCallback } from 'react';
import { OfferActions } from './OfferActions';
import {
  UPDATE_FIELD_NAME,
  FILE_UPLOAD_LIMIT,
  SCAR_UPDATE_FIELD_NAME,
} from './contants';
import {
  generateFiles,
  mappingS3UrlWithFile,
  generateOfferUploadUrl,
  convertFileToServerData,
  uploadFilesToS3,
  updateData,
} from 'helpers/S3';
import { OFFER_STEPS } from 'configs/constants';
import _get from 'lodash/get';
import { DynamicTextField } from 'fields/DynamicTextField';
import { File } from 'types';
import {
  getDownPayment,
  getOfferPrice,
  getSellers,
  isDisplayContingencyTable,
  getDate,
  getStatus,
  isShowStep200,
  isStep,
} from 'helpers/Offer';
import {
  TableRow,
  Table,
  TableHead,
  TableCell,
  TableBody,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  disabled: {
    pointerEvents: 'none',
    cursor: 'none',
  },
});

const SaveOffer = (props: SaveButtonProps) => {
  const notify = useNotify();
  const dataProvider = useDataProvider();
  const refresh = useRefresh();

  const uploadFiles = async (values: Array<File>, id: string, type: string) => {
    const planFields = generateFiles(values);
    const uploadUrl = generateOfferUploadUrl(id);
    const files = convertFileToServerData(planFields);
    const urls = await updateData(uploadUrl, files, type);
    const s3Files = mappingS3UrlWithFile(urls, planFields);
    return s3Files;
  };

  const handleSave = useCallback(
    async values => {
      try {
        const {
          SignedPurchaseAgreement,
          id,
          SCAR,
          CurrentStep: currentStep,
        } = values;
        const s3Files = isShowStep200(currentStep)
          ? await uploadFiles(SignedPurchaseAgreement, id, UPDATE_FIELD_NAME)
          : await uploadFiles(SCAR, id, SCAR_UPDATE_FIELD_NAME);
        await uploadFilesToS3(s3Files);
        notify('File uploaded');
        refresh();
      } catch (error) {
        notify(`Cannot upload files`, 'warning');
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [notify, dataProvider]
  );
  return <SaveButton {...props} onSave={handleSave} />;
};

const OfferEditToolbar: FC = props => {
  const currentStep = _get(props, ['record', 'CurrentStep']);
  return isShowStep200(currentStep) ||
    isStep(currentStep, OFFER_STEPS.reviewScarRequest) ? (
    <Toolbar {...props}>
      <SaveOffer submitOnEnter={true} />
    </Toolbar>
  ) : (
    <></>
  );
};

const OfferEdit: FC<EditProps> = props => {
  const classes = useStyles();

  return (
    <Edit actions={<OfferActions />} mutationMode="optimistic" {...props}>
      <SimpleForm toolbar={<OfferEditToolbar />}>
        <TextField source="Offer.OfferNo" label="ID" />
        <TextField
          source="Property.PropertyData.FullAddress"
          label="Property Address"
        />
        <DynamicTextField
          source="Property.Owners"
          label="Sellers"
          getValue={getSellers}
        />
        <DynamicTextField label="Offer Price" getValue={getOfferPrice} />
        <TextField source="Offer.Funding.PurchaseType" label="Funding" />
        <TextField source="Offer.Funding.LoanType" label="Loan Type" />
        <DynamicTextField
          source="Offer.Funding.DownPayment"
          label="Down Payment"
          getValue={getDownPayment}
        />
        <TextField source="Step.messages.advisor" label="Status" />
        <FormDataConsumer>
          {({ formData }) => {
            const { CurrentStep } = formData;
            return (
              <FileInput
                options={{ disabled: !isShowStep200(CurrentStep) }}
                source={UPDATE_FIELD_NAME}
                label="Signed Purchase Agreement"
                accept="application/pdf"
                multiple={true}
                maxSize={FILE_UPLOAD_LIMIT}
                validate={
                  isStep(CurrentStep, OFFER_STEPS.paBeingSigned)
                    ? [required()]
                    : undefined
                }
                helperText="Please upload the signed Purchase Agreement to proceed."
                className={!isShowStep200(CurrentStep) ? classes.disabled : ''}
              >
                <FileField source="url" title="name" />
              </FileInput>
            );
          }}
        </FormDataConsumer>
        <FormDataConsumer>
          {({ formData }) => {
            const { CurrentStep } = formData;
            return (
              <FileInput
                options={{
                  disabled: !isStep(CurrentStep, OFFER_STEPS.reviewScarRequest),
                }}
                source={SCAR_UPDATE_FIELD_NAME}
                label="SCAR"
                accept="application/pdf"
                multiple={true}
                maxSize={FILE_UPLOAD_LIMIT}
                validate={
                  isStep(CurrentStep, OFFER_STEPS.reviewScarRequest)
                    ? [required()]
                    : undefined
                }
                className={
                  !isStep(CurrentStep, OFFER_STEPS.reviewScarRequest)
                    ? classes.disabled
                    : ''
                }
                helperText="Please upload the Scar to proceed."
              >
                <FileField source="url" title="name" />
              </FileInput>
            );
          }}
        </FormDataConsumer>
        <FormDataConsumer>
          {({ formData }) => {
            const { CurrentStep } = formData;
            return (
              isDisplayContingencyTable(CurrentStep) && (
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Type</TableCell>
                      <TableCell>Due Date</TableCell>
                      <TableCell>Status</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    <TableRow>
                      <TableCell>Finance Contingency Removal</TableCell>
                      <TableCell>
                        <DynamicTextField
                          source="Offer.Contingencies.FinanceContingencyDueDate"
                          label="Finance Contingency Due Date"
                          getValue={getDate}
                        />
                      </TableCell>
                      <TableCell>
                        <DynamicTextField
                          source="Offer.Contingencies.FinanceContingencyDueDate"
                          label="Status"
                          getValue={getStatus}
                        />
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell>Inspection Contingency Removal</TableCell>
                      <TableCell>
                        <DynamicTextField
                          source="Offer.Contingencies.InspectionContingencyDueDate"
                          label="Inspection Contingency Due Date"
                          getValue={getDate}
                        />
                      </TableCell>
                      <TableCell>
                        <DynamicTextField
                          source="Offer.Contingencies.InspectionContingencyDueDate"
                          label="Status"
                          getValue={getStatus}
                        />
                      </TableCell>
                    </TableRow>
                    <TableRow>
                      <TableCell>Homesale Contingency Removal</TableCell>
                      <TableCell>
                        <DynamicTextField
                          source="Offer.Contingencies.SaleOfHomeContingencyDueDate"
                          label="Sale Of Home Contingency Due Date"
                          getValue={getDate}
                        />
                      </TableCell>
                      <TableCell>
                        <DynamicTextField
                          source="Offer.Contingencies.SaleOfHomeContingencyDueDate"
                          label="Status"
                          getValue={getStatus}
                        />
                      </TableCell>
                    </TableRow>
                  </TableBody>
                </Table>
              )
            );
          }}
        </FormDataConsumer>
      </SimpleForm>
    </Edit>
  );
};

export default OfferEdit;
