import React, { FC } from 'react';
import { TopToolbar } from 'react-admin';
import { ButtonAction } from './ButtonAction';
import _get from 'lodash/get';
import { OFFER_STEPS } from 'configs/constants';
import {
  isPABeingSigned,
  isDocumentExits,
  isOfferActionStep,
} from 'helpers/Offer';
import { advisorAction, actionsText } from './configs/advisorAction';
import { UPDATE_FIELD_NAME, SCAR_UPDATE_FIELD_NAME } from './contants';
export const OfferActions: FC = props => {
  // use lodash _get, avoid props validated
  const resource = _get(props, 'resource');
  const offer = _get(props, 'data');
  const offerId = _get(offer, 'id');
  const currentStep = _get(offer, 'CurrentStep', 0);
  const actions = _get(advisorAction, currentStep, []);

  const renderButtonAction = (
    actions: Array<number> = [],
    resource: string,
    offerId: string
  ) => {
    return actions.map((actionNumber: number) => {
      const text = _get(actionsText, actionNumber, '');
      return (
        <ButtonAction
          key={`${offerId}-${actionNumber}`}
          text={text}
          resource={resource}
          payload={{
            id: `${offerId}/steps`,
            data: { new_step: actionNumber },
          }}
        />
      );
    });
  };

  return (
    <TopToolbar>
      {isPABeingSigned(offer) && isDocumentExits(offer, UPDATE_FIELD_NAME) && (
        <ButtonAction
          text="PA Signed"
          resource={resource}
          payload={{
            id: `${offerId}/steps`,
            data: { new_step: OFFER_STEPS.confirmTransfer },
          }}
        />
      )}
      {isOfferActionStep(offer, OFFER_STEPS.reviewScarRequest) &&
        isDocumentExits(offer, SCAR_UPDATE_FIELD_NAME) && (
          <ButtonAction
            text="Confirm"
            resource={resource}
            payload={{
              id: `${offerId}/steps`,
              data: { new_step: OFFER_STEPS.removeContingencies },
            }}
          />
        )}
      {renderButtonAction(actions, resource, offerId)}
    </TopToolbar>
  );
};
