import _get from 'lodash/get';
import _unset from 'lodash/unset';
import _isEmpty from 'lodash/isEmpty';

type MonetaryRowProps = {
  inspection_report: string;
  description: string;
  request: string;
  monetary_amount: number;
};

const REQUEST_VALUES = {
  FIX: 'Fix',
  MONETARY: 'Monetary',
};

const checkMonetaryDataRow = (row: MonetaryRowProps) => {
  const hasFullData = [
    'inspection_report',
    'description',
    'request',
    'monetary_amount',
  ].every((prop: string) => _get(row, prop));
  if (!hasFullData) return false;
  if (_get(row, 'request') === REQUEST_VALUES.MONETARY)
    return _get(row, 'monetary_amount');
  return true;
};

export const getMonetaryTotalAmount = function (
  this: any,
  params: string[]
): number {
  if (_isEmpty(params)) return 0;
  const [matrixName] = params;

  const matrix = this.survey.getQuestionByName(matrixName);
  if (!_get(matrix, 'value')) return 0;

  return matrix.value.reduce((sum: number, row: MonetaryRowProps) => {
    if (
      checkMonetaryDataRow(row) &&
      _get(row, 'request') === REQUEST_VALUES.MONETARY
    )
      return sum + +_get(row, 'monetary_amount', 0);
    return sum;
  }, 0);
};

export const triggerMonetaryData = function (
  this: any,
  params: string[]
): void {
  if (_isEmpty(params)) return;
  const [matrixName] = params;

  const matrix = this.survey.getQuestionByName(matrixName);
  if (!_get(matrix, 'value')) return;

  matrix.value = [...matrix.value].map((row: MonetaryRowProps) => {
    if (_get(row, 'request') === REQUEST_VALUES.FIX) {
      _unset(row, 'monetary_amount');
    }
    return row;
  });
};
